@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a{
  text-decoration: none;
  color: lightsalmon ;
}


body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #282c34;
  color: whitesmoke;
  max-width: 80%;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{padding:20px;text-align:left}.won-card{background-color:#449220}.lost-card{background-color:#5c1414}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-result,.MatchDetailCard .match-venue{margin-top:10px}.MatchDetailCard .additional-details{text-align:right}.MatchDetailCard .additional-details>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-date,.MatchSmallCard .match-result,.MatchSmallCard .match-venue{margin-top:10px}.MatchSmallCard .additional-details{text-align:right}.MatchSmallCard .additional-details>p{margin-bottom:20px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:3.6rem;margin-bottom:20px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .win-loss-section{justify-self:center;padding:35px;text-align:center}.TeamPage .more-link{justify-self:center;align-self:center;font-size:1.5rem}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:10px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px}

.YearSelector{text-align:center;list-style-type:none}.YearSelector li{margin-top:20px}

.HomePage .team-list{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.HomePage .team-list>div{text-align:center;height:85px;border:1px solid #ccc;padding:20px}.HomePage .app-Name{font-size:4em;font-weight:bold;color:lawngreen;text-align:center;margin:20px auto}

