.TeamPage{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;

    .team-name-section{
        grid-column: span 3;
        justify-self: start;
        align-self: end;
    }
    .team-name{
        font-size: 3.6rem;
        margin-bottom: 20px;
    }
    .match-detail-section{
        grid-column: span 4
    }
    .win-loss-section{
        justify-self: center;
        
        padding: 35px;
        text-align: center;
    }
    .more-link{
        justify-self:center;
        align-self: center;
        font-size: 1.5rem;
    }
}

